@import url('https://fonts.googleapis.com/css2?family=Codystar&display=swap');
.header {
    width: 100%;
    height: 100px;
    background: rgb(253, 253, 254);
    background: radial-gradient(circle, var(--main-light-color) 0%, rgba(182, 225, 237, 1) 45%, var(--main-dark-color) 100%);
    margin: 0 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 1s ease-in-out;
    position: relative;
}

.header h2 {
    font-family: 'Codystar', cursive;
    font-size: 64px;
    color: var(--main-dark-color);
    text-shadow: 0 0 10px rgba(69, 122, 133, 0.5);
    display: block;
    letter-spacing: 2px;
    transition: all 1s ease-in-out;
}

.header:hover {
    background: radial-gradient(circle, var(--main-light-color) 0%, rgba(182, 225, 237, 1) 55%, var(--main-dark-color) 100%);
}

.header:hover h2 {
    letter-spacing: 6px;
}

.login_block {
    display: block;
    position: absolute;
    right: 0;
    top: 50%;
    padding: 10px;
    margin-right: 20px;
    border: 2px solid var(--main-light-color);
    border-radius: 5px;
    transform: translateY(-50%);
}